
@include bundle($type: 'shared') {
  $type: 'split';

  .andes-button-#{$type} {
    display: inline-block;
    font-family: $font-family-primary;
    position: relative;

    &__menu {
      padding: $andes-spacing-8 0;
      font-size: $font-size-16;
      text-align: left;
      list-style: none;
      background-color: $andes-white;
      -webkit-background-clip: padding-box;
      background-clip: padding-box;
      border-radius: 6px;
      box-shadow: $shadow-elevated;

      li {
        padding: 1px 0;
        display: flex;
      }

      a,
      button {
        @include add-focus-visible;
        @include focus-relative-fix;

        width: 100%;
        padding: 6.5px 1em;
        display: block;
        cursor: pointer;
        text-align: start;
        background-color: $andes-white;
        font-family: $font-family-primary;
        font-weight: $font-weight-light;
        color: $andes-text-color-primary;
        text-decoration: none;
        font-size: $font-size-14;
        border: 0;
      }

      a:hover,
      button:hover {
        background-color: $andes-gray-040;
      }
    }
  }

  .andes-button-split {
    .andes-button-split-text {
      @include focus-relative-fix;

      display: inline;
      padding-left: $andes-spacing-16;
      padding-right: $andes-spacing-16;
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
    }

    .andes-button-split-chevron {
      @include focus-relative-fix;

      vertical-align: bottom;
      border-left: 1px $button-chevron-border-color solid;
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
      padding: 0 6px;
    }

    &.andes-button--loud .andes-button-split__svg-fill {
      fill: $andes-white;
    }

    &.andes-button--transparent .andes-button-split__svg-fill,
    &.andes-button--quiet .andes-button-split__svg-fill {
      fill: $andes-blue-500;
    }

    .andes-button--loud .andes-button-split__svg-fill {
      fill: $andes-white;
    }

    .andes-button--transparent .andes-button-split__svg-fill,
    .andes-button--quiet .andes-button-split__svg-fill {
      fill: $andes-blue-500;
    }
  }
}
