@include bundle($type: 'shared') {
  .andes-button--medium {
    font-size: $button-font-size-medium;
    line-height: $button-height-medium;
    padding: $button-padding-medium;
    height: $button-height-medium;
    border-radius: $border-radius-5;

    .andes-button__text + svg,
    svg + .andes-button__text {
      margin-left: $button-spacing-8;
    }
  }
}
