.andes-tag-collapsed {
  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  &__list-item {
    display: inline-flex;
  }

  &__wrap {
    position: relative;
  }

  &__more {
    padding: $andes-spacing-8;
    display: block;
    width: 200px;
    border-radius: $border-radius-6;
    box-shadow: $shadow-elevated;
    background-color: $andes-white;

    .andes-tag {
      &--large {
        margin: 4px;
      }

      &--small {
        margin: 3px;
      }

      &__label {
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        max-width: 148px;
      }
    }
  }
}

.andes-tag-collapsed .andes-tag {
  &--large {
    margin-right: 8px;
  }

  &--small {
    margin-right: 6px;
  }
}
