@include bundle($type: 'shared') {
  $map: $button-loud;
  $type: 'loud';

  .andes-button--#{$type} {
    cursor: pointer;
    transition: box-shadow 0.25s ease-out, background-color 0.2s ease-out;
    box-shadow: 0 0 0 0 map-deep-get($map, 'box-shadow');

    &,
    &:link,
    &:visited,
    &:focus {
      @include button-shared-styles($map: $map, $type: 'default');
    }

    &:hover {
      @include button-shared-styles($map: $map, $type: 'hover');
      transition: background-color 0.2s ease-in;
    }

    &:active {
      @include button-shared-styles($map: $map, $type: 'pressed');
    }

    &:disabled,
    &[disabled] {
      @include button-shared-styles($map: $map, $type: 'disabled');
      cursor: default;
      background-clip: padding-box; // needed to make borders match backgrounds
    }
  }
}
