@mixin colorvalues($base-color) {
  border-color: $base-color;
  background-color: transparentize($base-color, 0.9);

  .andes-tag__label {
    color: $base-color;
  }

  .andes-tag__svg-fill {
    fill: $base-color;
  }

  .andes-tag__avatar-text-container,
  .andes-tag__avatar-icon-container {
    background-color: transparentize($base-color, 0.9);
    color: transparentize($base-color, 0.3);
  }

  .andes-tag__close:hover {
    background-color: transparentize($base-color, 0.9);
  }

  &.andes-tag--disabled {
    border-color: transparentize($base-color, 0.9);

    .andes-tag__label {
      color: transparentize($base-color, 0.7);
    }

    .andes-tag__avatar-text-container,
    .andes-tag__avatar-icon-container {
      color: transparentize($base-color, 0.7);
    }
  }
}
