.context-selector {
  outline: none;
  width: 280px;
  background-color: $andes-white;
  margin-top: -2px;

  .andes-datepicker__container {
    width: 100%;
  }

  .andes-datepicker__caption-container {
    display: flex;
    justify-content: center;
    margin-bottom: 24px;
    line-height: 1;

    div {
      @include chevron('bottom');

      position: relative;
      font-size: 16px;
      -webkit-font-smoothing: antialiased;
      font-weight: $font-weight-regular;

      &::before {
        position: absolute;
        right: -19px;
        width: 20px;
        height: 20px;
        content: '';
        box-sizing: content-box;
        top: -1px;
      }
    }
  }

  &__context-line {
    border-top: $andes-gray-100 solid 1px;
    height: 1px;
  }

  &__context-wrapper {
    z-index: 1;
    display: flex;
    flex-direction: row;
    flex-flow: row wrap;
    justify-content: space-between;
    padding: $andes-spacing-12 8px 4px;
    background-color: $andes-white;
  }

  &__month-cell {
    @include add-focus-visible($datepicker-control-box-shadow-focus-visible);

    display: flex;
    justify-content: center;
    align-items: center;
    margin: $andes-spacing-12;
    width: $datepicker-month-cell-width;
    height: $datepicker-month-cell-height;
    border-radius: $andes-spacing-16;
    font-size: $font-size-14;
    -webkit-font-smoothing: antialiased;
    cursor: pointer;
    user-select: none;
    text-transform: capitalize;
    outline: none;

    &:hover {
      background-color: $andes-blue-150;
    }

    &:active {
      color: $andes-white;
      background-color: $andes-blue-500;
    }

    &--current {
      font-weight: $font-weight-bold;
    }

    &--disabled {
      cursor: not-allowed;
      color: $andes-gray-100;

      &:hover {
        background-color: inherit;
      }

      &:active {
        color: $andes-gray-100;
        background-color: inherit;
      }
    }
  }
}
