@include bundle($type: 'shared') {
  .andes-button--small {
    font-size: $button-font-size-small;
    line-height: $button-height-small;
    padding: $button-padding-small;
    height: $button-height-small;
    border-radius: $border-radius-4;

    svg {
      display: none;
    }

    .andes-button__text {
      margin: 0;
    }
  }
}
