@import '../components/ContextSelector/styles';

// Global requirements
@include bundle($type: 'shared') {
  .andes-datepicker {
    position: relative;
    max-width: $datepicker-container;

    &__container {
      display: inline-block;
    }

    &__wrapper {
      @include add-focus-visible($datepicker-control-box-shadow-focus-visible);
      position: relative;
    }

    &__daypicker-wrapper {
      display: inline-block;
      position: relative;
    }

    &__button-wrapper {
      margin-top: 16px;
    }

    &__navbar {
      position: relative;
      display: flex;
      justify-content: center;

      &-button {
        @include add-focus-visible($datepicker-control-box-shadow-focus-visible);

        &:focus {
          position: absolute;
        }

        &:focus-visible {
          position: absolute;
        }

        outline: none;
        position: absolute;
        top: 0;
        width: 16px;
        height: 16px;
        cursor: pointer;

        // reset
        font: inherit;
        padding: 0;
        border: 0;
        background-color: transparent;

        &:hover:not(&-interaction--disabled) {
          opacity: 0.8;
        }

        &--prev {
          left: 2px;
          background-image: url($datepicker-navbar-button-prev);
        }

        &--next {
          right: 4px;
          background-image: url($datepicker-navbar-button-next);
        }

        &-interaction--disabled {
          opacity: 0.25;
        }

        &--center {
          @include chevron('top');
          @include add-focus-visible($datepicker-control-box-shadow-focus-visible);

          position: relative;
          display: inline-block;
          line-height: 1;
          cursor: pointer;
          user-select: none;
          color: $datepicker-control-color-primary;

          // reset
          font-size: $datepicker-control-font-size-default;
          font-family: $font-family-primary;
          font-weight: $font-weight-regular;
          -webkit-font-smoothing: antialiased;
          line-height: 1;
          padding: 0;
          border: 0;
          background-color: transparent;
          text-transform: capitalize;

          &::before {
            position: absolute;
            right: -19px;
            width: 20px;
            height: 20px;
            content: '';
            box-sizing: content-box;
            top: -1px;
          }
        }
      }
    }

    &__caption {
      display: table-caption;
      margin-bottom: 24px;
      line-height: 1;
      text-align: center;
      font-size: $datepicker-control-font-size-default;
      font-family: $font-family-primary;
      font-weight: $font-weight-regular;
      color: $datepicker-control-color-primary;
    }

    &__weekdays-container {
      display: table-header-group;
    }

    &__weekdays-row {
      display: table-row;
    }

    &__weekday-cell {
      display: table-cell;
      width: 40px;
      height: 24px;
      font-size: $datepicker-weekday-font-size;
      -webkit-font-smoothing: antialiased;
      color: $andes-text-color-secondary;
      text-align: center;
      text-transform: uppercase;
      vertical-align: top;
      outline: none;

      abbr {
        text-decoration: none;
      }
    }

    &__body {
      display: flex;
      flex-direction: column;
    }

    &__week-row {
      display: flex;
    }

    &__day-cell {
      @include add-focus-visible($datepicker-control-box-shadow-focus-visible);
      @include focus-relative-fix;

      display: flex;
      flex-grow: 1;
      justify-content: center;
      align-items: center;

      height: 40px;
      font-size: $datepicker-day-font-size;
      color: $andes-text-color-primary;
      text-align: center;
      vertical-align: middle;
      cursor: pointer;

      span {
        position: relative;
        display: inline-block;
        width: 32px;
        height: 32px;
        vertical-align: middle;
        line-height: 32px;
        -webkit-font-smoothing: antialiased;
        font-weight: $font-weight-regular;
      }

      &:hover {
        span {
          @include day-cell-selected($datepicker-day-cell-hover, $andes-gray-900);
        }
      }

      &--today span {
        font-weight: $font-weight-bold;
      }

      &--selected,
      &--selected:hover,
      &--selected.andes-datepicker__day-cell--outside {
        span {
          @include day-cell-selected();
        }
      }

      &--disabled,
      &--disabled:hover {
        cursor: default;

        span {
          color: $andes-text-color-disabled;
        }
      }

      &--outside,
      &--outside:hover {
        cursor: default;

        span {
          color: $andes-text-color-disabled;
        }
      }
    }

    // Modifiers
    // Modifiers - Ranges
    @include day-cell-ranges('primary');

    @include day-cell-ranges('secondary', $datepicker-range-secondary-color, $datepicker-range-secondary-color-quiet);

    // Modifiers - Events
    &__has-event {
      position: relative;

      &:after {
        content: '';
        position: absolute;
        z-index: 1;
        left: 50%;
        top: 71%;
        margin-left: -3px;
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background-color: $andes-blue-500;
      }

      &.andes-datepicker__day-cell--disabled,
      &.andes-datepicker__day-cell--outside {
        &:after {
          background-color: $andes-text-color-disabled;
        }
      }

      &.andes-datepicker__day-cell--selected {
        &:after {
          background-color: $andes-white;
        }
      }
    }
  }
}
