@include bundle($type: 'shared') {
  .andes-tag {
    display: inline-flex;
    align-items: center;
    font-weight: $tag-font-weight;
    font-family: $font-family-primary;
    user-select: none;
    background-color: transparent;
    border: $tag-border-width solid $tag-border-color;
    width: auto;
    color: $tag-font-color;
    box-sizing: border-box;
    width: auto;

    &--small {
      font-size: $tag-font-size-small;
      height: $tag-heigth-small;
      padding: 0 $tag-padding-small;
      border-radius: $tag-border-radius-small;

      .andes-tag__label {
        padding: 0  $tag-label-padding-small;
        font-size: inherit;
      }

      .andes-tag__close {
        font-size: inherit;
        height: $tag-close-size-small;
        width: $tag-close-size-small;
        margin-left: $tag-close-margin-small;
      }

      .andes-tag__child {
        padding: 0 $tag-child-padding-small;
      }

      .andes-tag__avatar {
        width: $tag-avatar-size-small;
        height: $tag-avatar-size-small;

        &-text-container {
          font-size: $tag-avatar-font-size-small;
        }
      }
    }

    &--large {
      font-size: $tag-font-size-large;
      height: $tag-heigth-large;
      padding: 0 $tag-padding-large;
      border-radius: $tag-border-radius-large;

      .andes-tag__label {
        padding: 0 $tag-label-padding-large;
        font-size: inherit;
      }

      .andes-tag__close {
        font-size: inherit;
        height: $tag-close-size-large;
        width: $tag-close-size-large;
        margin-left: $tag-close-margin-large;
      }

      .andes-tag__child {
        padding: 0 $tag-child-padding-large;
      }

      .andes-tag__avatar {
        width: $tag-avatar-size-large;
        height: $tag-avatar-size-large;

        &-text-container {
          font-size: $tag-avatar-font-size-large;
        }
      }
    }

    &__label {
      color: inherit;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }

    &__close {
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      cursor: pointer;
      border: 0;
      padding: 0;
      background-color: transparent;
      outline: none;
      transition: background-color 0.1s ease-out;

      &:hover {
        background-color: $tag-close-backgroud-hover;
        transition: background-color 0.1s ease-in;
      }

      @include add-focus-visible;
    }

    &--disabled {
      border-color: $tag-border-disabled-color;

      .andes-tag__label {
        color: $tag-label-disabled-color;
      }

      .andes-tag__close {
        cursor: default;

        &:hover {
          background-color: transparent;
        }
      }

      .andes-tag__avatar-text-container,
      .andes-tag__avatar-icon-container {
        color: $tag-label-disabled-color;
      }
    }

    &__avatar {
      border-radius: 50%;
      overflow: hidden;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      justify-content: center;

      &-image,
      &-icon-container,
      &-text-container {
        height: 100%;
        width: 100%;
        border-radius: inherit;
      }

      &-icon-container,
      &-text-container {
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: $tag-avatar-backgroud;
        color: $tag-avatar-color;
      }

      &-icon-container {
        > * {
          height: $tag-avatar-icon-size;
          width: $tag-avatar-icon-size;
        }
      }

      &-text-container {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }

      &-image {
        visibility: hidden;
        object-fit: contain;

        &--loaded {
          visibility: visible;
        }

        &--landscape {
          max-height: 100%;
          max-width: none;
        }
      }
    }

    &--selectable {
      &:hover {
        background-color: $tag-background-hover;
      }
    }

    &--clickable {
      @include add-focus-visible;

      cursor: pointer;
    }

    &__icon-container {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
}
