$main-class: '.andes-button';
$progress: 'progress';
$loud: 'loud';
$quiet: 'quiet';
// phases
$loading-phase: 'loading';
$finished-phase: 'finished';

#{$main-class} {
  &--#{$quiet} {
    &.#{$loading-phase}:hover,
    &.#{$loading-phase}:active,
    &.#{$loading-phase}:disabled {
      @include button-shared-styles($map: $button-quiet, $type: 'default');
    }

    &::after,
    &::before {
      @include andes-button-layer-animation($map: $button-quiet);
    }
  }

  &--#{$loud} {
    &.#{$loading-phase}:hover,
    &.#{$loading-phase}:active,
    &.#{$loading-phase}:disabled {
      @include button-shared-styles($map: $button-loud, $type: 'default');
    }

    &::after,
    &::before {
      @include andes-button-layer-animation($map: $button-loud);
    }
  }

  &--#{$progress} {
    position: relative;
    overflow: hidden;
    border: 0;

    &:focus {
      @include add-focus-box-shadow;
    }

    .andes-button__content {
      position: relative;
      z-index: 1;
      width: max-content;
      margin: auto;

      #{$main-class}--#{$progress}__content {
        position: absolute;
        transform: translate3d(0, 32px, 0);
        transition: transform 200ms ease-in;
      }

      *:not(#{$main-class}--#{$progress}__content) {
        transform: translate3d(0, 0, 0);
        transition: transform 200ms ease-in;
      }
    }

    &.#{$loading-phase} {
      .andes-button__content {
        #{$main-class}--#{$progress}__content {
          transform: translate3d(0, 0, 0);
          transition: transform 200ms ease-in;
          position: relative;
        }

        *:not(#{$main-class}--#{$progress}__content) {
          position: absolute;
          width: 100%;
          transform: translate3d(0, -32px, 0);
          transition: transform 200ms ease-in;
        }
      }

      &::after,
      &:focus::after {
        transform: scaleX(1);
        animation: andes-button-start-width-animation 60s cubic-bezier(0, 0.56, 0.76, 0.96);
      }
    }

    &.#{$progress}::before,
    &.#{$finished-phase}::before,
    &.#{$loading-phase}.#{$finished-phase} {
      @include andes-button-finish-animation;
    }
  }
}
