@mixin day-cell-selected($background: $datepicker-day-cell-selected, $color: $andes-white) {
  border-radius: 50%;
  color: $color;
  background-color: $background;
}

@mixin draw-range-ends(
  $side,
  $primary-color,
  $secondary-color: null,
) {
  @if $side == 'left' or $side == 'right' {
    @include range-end($side, $primary-color, $secondary-color);
  } @else {
    @include range-mid($primary-color);
  }
}

@mixin range-end($side, $primary-color, $secondary-color) {
  span {
    @include day-cell-selected($primary-color);

    &:before {
      content: '';
      position: absolute;

      @if $side == 'left' {
        left: -4px;
        border-bottom-right-radius: 32px;
        border-top-right-radius: 32px;
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
      } @else {
        right: -4px;
        border-bottom-right-radius: 0;
        border-top-right-radius: 0;
        border-top-left-radius: 32px;
        border-bottom-left-radius: 32px;
      } 

      width: 36px;
      height: 32px;
      background-color: $secondary-color;
    }
  }
}

@mixin range-mid($color) {
  background: linear-gradient(0deg, $andes-white 10%, $color 10%, $color 90%, $andes-white 90%);
}

@mixin day-cell-ranges(
  $order,
  $primary-color: $datepicker-range-primary-color,
  $secondary-color: $datepicker-range-primary-color-quiet,
) {
  &__range-#{$order}--start,
  &__range-#{$order}--start:hover {
    @include draw-range-ends('right', $primary-color, $secondary-color);
  }

  &__range-#{$order}--mid {
    @include draw-range-ends('mid', $secondary-color);
  }

  &__range-#{$order}--entered-to {
    @include draw-range-ends('left', $secondary-color, $secondary-color);

    span {
      color: $andes-text-color-primary;
    }
  }

  &__range-#{$order}--end {
    @include draw-range-ends('left', $primary-color, $secondary-color);
  }
}

@mixin chevron($direction) {
  margin-left: -12px;

  &:after {
    position: absolute;

    right: -12px;
    width: 5px;
    height: 5px;
    background: inherit;
    border-right: 1px solid $andes-gray-900;
    border-bottom: 1px solid $andes-gray-900;
    content: '';
    box-sizing: content-box;

    @if $direction == 'top' {
      top: 4px;
      transform: rotate(45deg);
    } @else {
      top: 8px;
      transform: rotate(225deg);
    }

    transition: border-color 0.18s ease-out;
  }
}
