@include bundle($type: 'shared') {
  $type: 'dropdown';

  .andes-button-#{$type} {
    padding-right: 16px;

    &__menu {
      z-index: 1000;
      padding: $andes-spacing-8 0;
      font-size: $font-size-16;
      text-align: left;
      list-style: none;
      background-color: $andes-white;
      -webkit-background-clip: padding-box;
      background-clip: padding-box;
      border-radius: 6px;
      box-shadow: $shadow-elevated;

      li {
        display: flex;
        padding: 1px 0;
      }

      a,
      button {
        @include add-focus-visible;
        @include focus-relative-fix;

        cursor: pointer;
        display: block;
        width: 100%;
        text-align: start;
        background-color: $andes-white;
        font-family: $font-family-primary;
        font-weight: $font-weight-light;
        color: $andes-text-color-primary;
        text-decoration: none;
        font-size: $font-size-14;
        border: 0;
        padding: 6.5px $andes-spacing-16;
      }

      .andes-button-dropdown__menu-item:hover {
        background: $andes-gray-040;
      }
    }

    &.andes-button--loud .andes-button-dropdown__svg-fill {
      fill: $andes-white;
    }

    &.andes-button--transparent .andes-button-dropdown__svg-fill,
    &.andes-button--quiet .andes-button-dropdown__svg-fill {
      fill: $andes-blue-500;
    }
  }

  .andes-button-#{$type}-container {
    display: inline-block;
    position: relative;
  }
}
