// Shared styles for calendar implementations
.andes-datepicker {
  &-control {
    min-width: $datepicker-container;
  }

  &-control__label {
    display: block;
    height: 18px;
    padding: 0 $datepicker-spacing-6-default;
    margin-bottom: $datepicker-control-spacing-4;
    font-family: $font-family-primary;
    font-weight: $font-weight-regular;
    font-size: $datepicker-control-font-size-label;
    color: $datepicker-control-color-primary;
    line-height: $line-height-s;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    -webkit-font-smoothing: antialiased;
  }

  &-control__message {
    font-size: $datepicker-control-font-size-message;
    padding-left: $datepicker-spacing-6-default;
    margin: $datepicker-spacing-6-default 0 0;
    display: flex;
    align-items: center;
    color: $datepicker-control-color-secondary;
    font-family: $font-family-primary;
    font-weight: $font-weight-regular;
    line-height: $line-height-base;
    -webkit-font-smoothing: antialiased;
  }

  &__control-error-icon {
    @include square(12px);
    border-radius: 50%;
    margin-right: $datepicker-spacing-6-default;
    background-color: $datepicker-control-color-red;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__trigger-wrapper {
    width: 100%;
    height: $datepicker-trigger-height;
    border-radius: $datepicker-control-border-radius;
    background-color: transparent;
    position: relative;
  }

  &__trigger {
    @include add-focus-visible($datepicker-control-box-shadow-focus-visible);

    &:focus:not(:focus-visible) {
      box-shadow: $datepicker-control-box-shadow-focus;
      -moz-box-shadow: $datepicker-control-box-shadow-focus;
      -webkit-box-shadow: $datepicker-control-box-shadow-focus;
    }

    margin: 0;
    border: 0;
    outline: none;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: $datepicker-control-spacing-vertical $datepicker-control-spacing-horizontal;
    font-size: $datepicker-control-font-size-default;
    border-radius: inherit;
    background-color: $datepicker-control-background-default;
    box-shadow: $datepicker-control-box-shadow-default;
    cursor: pointer;
  }

  &__trigger-content {
    flex-grow: 1;
    margin-right: $andes-spacing-12;
    text-align: start;
    font-family: $font-family-primary;
    font-size: inherit;
    line-height: $line-height-s;
    color: $datepicker-control-color-primary;
    -webkit-font-smoothing: antialiased;
  }

  &__trigger-content--hidden {
    color: transparent !important;
  }

  &__trigger-content--placeholder {
    color: $datepicker-control-color-secondary;
  }

  &__trigger-icon {
    display: inherit;
    width: 20px;
    align-content: flex-end;
  }

  &__trigger-content-list {
    position: absolute;
    bottom: $datepicker-control-spacing-vertical;
    left: $datepicker-control-spacing-horizontal;
  }

  // Modifiers
  &-control--error {
    .andes-datepicker-control__label {
      color: $datepicker-control-color-red;
    }

    .andes-datepicker-control__message {
      color: $datepicker-control-color-red;
      font-weight: $font-weight-semibold;
    }

    .andes-datepicker__trigger {
      box-shadow: $datepicker-control-box-shadow-error;

      &:focus {
        box-shadow: $datepicker-control-box-shadow-error-focus;
      }
    }
  }

  &-control--disabled {
    .andes-datepicker-control__label {
      color: $datepicker-control-color-disabled;
    }

    .andes-datepicker-control__message {
      color: $datepicker-control-color-disabled;
    }

    .andes-datepicker__trigger {
      background-color: $datepicker-control-background-disabled;
      border: $datepicker-control-border-disabled;
      box-shadow: none;
      cursor: not-allowed;
    }

    .andes-datepicker__trigger-content {
      color: $datepicker-control-color-disabled;
    }

    .andes-datepicker__trigger-icon > svg > path {
      fill: $datepicker-control-color-disabled;
    }
  }

  // Overlay
  &__overlay {
    box-sizing: border-box;
    position: absolute;
    top: 70px;
    left: 0;
    padding: 32px 20px 24px;
    width: $datepicker-container;
    border-radius: $border-radius-6;
    box-shadow: $datepicker-overlay-shadow;
    background-color: $datepicker-overlay-background;
    z-index: 500;
    margin-top: $datepicker-spacing-6-default;
    outline: none;
  }

  &__overlay--compact {
    top: 48px;
  }
}
