$datepicker-container: 320px;
$datepicker-overlay-shadow: 0 6px 16px 0 $andes-gray-100;
$datepicker-overlay-background: $andes-white;

$datepicker-caption-font-size: 16px;
$datepicker-weekday-font-size: 10px;
$datepicker-day-font-size: 14px;

$datepicker-day-cell-hover: $andes-blue-100;
$datepicker-day-cell-selected: $andes-blue-500;

$datepicker-range-primary-color: $andes-blue-500;
$datepicker-range-primary-color-quiet: $andes-blue-100;

$datepicker-range-secondary-color: $andes-orange-500;
$datepicker-range-secondary-color-quiet: #ffe7db;


$datepicker-navbar-button-prev: 'data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSIxNiIgaGVpZ2h0PSIxNiI+PHBhdGggZD0iTTEyLjEyNiAxLjk5M2wtMS4wNi0xLjA2TDQgNy45OTdsNy4wNyA3LjA3IDEuMDYtMS4wNi02LTZ6IiBmaWxsPSIjMDAwIiBmaWxsLXJ1bGU9Im5vbnplcm8iIGZpbGwtb3BhY2l0eT0iLjQ1Ii8+PC9zdmc+';
$datepicker-navbar-button-next: 'data:image/svg+xml;base64,';

$datepicker-month-cell-width: 56px;
$datepicker-month-cell-height: 32px;

$datepicker-spacing-6-default: 6px;
$datepicker-trigger-height: 48px;

// Box-shadow
$datepicker-control-box-shadow-default: 0 0 0 1px $andes-gray-250;
$datepicker-control-box-shadow-focus: inset 0 0 0 2px $andes-blue-500;
$datepicker-control-box-shadow-focus-visible: 0 0 0 2px $andes-white, 0 0 0 3px $andes-blue-600, 0 0 0 5px $andes-blue-300;
$datepicker-control-box-shadow-error: 0 0 0 1px $andes-error-red;
$datepicker-control-box-shadow-error-focus: 0 0 0 2px $andes-error-red;

// Border
$datepicker-control-border-disabled: 1px dashed $andes-gray-250;
$datepicker-control-border-radius: $border-radius-6;

// Font size
$datepicker-control-font-size-default: $font-size-16;
$datepicker-control-font-size-label: $font-size-14;
$datepicker-control-font-size-message: 13px;

// Spacing
$datepicker-control-spacing-4: $andes-spacing-4;
$datepicker-control-spacing-vertical: 14px;
$datepicker-control-spacing-horizontal: $andes-spacing-12;

// Background
$datepicker-control-background-default: $andes-white;
$datepicker-control-background-disabled: transparentize($andes-black, 0.98);

// Color
$datepicker-control-color-red: $andes-error-red;
$datepicker-control-color-primary: $andes-text-color-primary;
$datepicker-control-color-secondary: $andes-text-color-secondary;
$datepicker-control-color-disabled: $andes-gray-250;

